* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "montseratt", sans-serif;
}
.App {
  background: url("./assets/cold.jpg");
  background-size: cover;
  transition: 0.4 ease-out;
  background-repeat: no-repeat;
  background-position: center;
}
.App.warm {
  background: url("./assets/hot.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}
main {
  min-height: 100vh;
  padding: 25px;
}
.search-box {
  width: 100%;
  margin: 0 auto 75px;
}
.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: #313131;
  font-size: 20px;

  transition: 400ms;
}
.search-box .search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}
.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}
.error {
  color: #fff;
  font-size: 25px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  margin: 0 auto 75px;
  text-align: center;
  background: rgba(199, 30, 30, 0.4);
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  width: fit-content;
  padding: 5px 10px;
  border-radius: 10px;
}
@media only screen and (max-width: 600px) {
  .weather-box .temp {
    font-size: 50px;
  }
}
